*,
*::before,
*::after {
	box-sizing: border-box;
	font-family: 'Poppins', sans-serif;
	font-weight: 400;
}

.flex {
	display: flex;
	align-items: flex-start;
	justify-content: flex-start;
}

.--just-space {
	justify-content: space-between;
}

.--just-center {
	justify-content: center;
}

.--just-end {
	justify-content: flex-end;
}

.--align-str {
	align-items: stretch;
}

.--align-center {
	align-items: center;
}

.--align-end {
	align-items: flex-end;
}

.--dir-col {
	flex-direction: column;
}

html {
	font-size: 16px;
}

html,
body {
	height: 100%;
	position: relative;

}

.wrapper {
	min-height: 100%;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
}
.main {
	flex: 1 1 auto;
	display: flex;
	flex-direction: column;
	justify-content: space-around;
}

body {
	position: relative;
	font-size: 1rem;
}

ul,
ol,
li,
p,
h1,
h2,
h3,
h4,
h5,
h6 {
	margin: 0;
}

$primary-color: #162C4E;
$secondary-color: #DF2224;
