.header {
  padding-top: 60px;
  position: relative;
  display: flex;
  justify-content: center;
  &::after, &::before {
    content: '';
    position: absolute;
    top: 0;
    display: block;
  }
  &::after {
    left: 0;
    height: 419px;
    width: 470px;
    background-image: url("../img/sprite.svg#sprite--left-background-cloud");
    background-repeat: no-repeat;
  }
  &::before {
    right: 0;
    width: 320px;
    height: 285px;
    background-image: url("../img/sprite.svg#sprite--right-background-cloud");
    background-repeat: no-repeat;
  }
  &__logo {
    @include btn_anim(1.05,1)
  }
}

@media (max-width: 1560px) and (min-width: 451px){
  .header {
    padding-top: 100px;
    &::after {
      height: 248px;
      width: 208px;
    }
    &::before {
      height: 239px;
      width: 250px;
    }
  }
}

@media (max-width: 451px) {
  .header {
    padding-top: 80px;
    &::after {
      height: 143px;
      width: 170px;
    }
    &::before {
      height: 215px;
      width: 184px;
    }
    &__logo {
      img {
        width: 128px;
      }
    }
  }
}