.footer {
  padding: 60px 0 72px;
  background-color: $primary-color;
  &__form {
    margin: 0 auto;
    position: relative;
    display: flex;
    width: 440px;
    justify-content: center;
    align-items: center;
  }
  &__form-input {
    padding: 16px 27px;
    min-width: 440px;
    background: #FFFFFF;
    border: 1px solid rgba(0, 0, 0, 0.8);
    box-shadow: 0 0 50px rgba(0, 0, 0, 0.4);
    border-radius: 40px;
    font-size: 18px;
    line-height: 150%;
    color: rgba(0, 0, 0, 0.6);
    transition: .2s;
    &:hover, &:focus {
      outline: 3px solid #ffffff;

    }
  }
  &__form-submit {
    @include no-btn();
    @include btn_anim(1.05,1);
    position: absolute;
    right: 8px;
    width: 43px;
    height: 43px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    background-color: $secondary-color;
    transition: .2s;
    &:hover, &:focus {
      background-color: $primary-color;
    }
  }
}

@media (max-width: 1100px) {
  .footer {
    padding: 60px 0;
    &__form {
      width: 360px;
    }
    &__form-input {
      padding: 10px 20px;
      min-width: 360px;
      font-size: 14px;
    }
    &__form-submit {
      width: 33px;
      height: 33px;
      right: 4px;
    }
  }
}

@media (max-width: 451px) {
  .footer {
    padding: 50px 0;
    &__form {
      width: 280px;
    }
    &__form-input {
      min-width: 280px;
      font-size: 13px;
    }
    &__form-submit {
      width: 33px;
      height: 33px;
      right: 4px;
    }
  }
}