.popup {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100vw;
  height: 100vh;
  z-index: 2;
  display: none;
  justify-content: center;
  align-items: center;
  background-color: rgba(22, 44, 78, 0.6);
  &__wrapper {
    padding: 16px 16px 70px;
    width: 320px;
    height: 370px;
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    background-color: #ffffff;
  }
  &__cross {
    @include no-btn();
    padding: 13px 16px;
    z-index: 3;
    align-self: flex-end
  }
  &__massage {
    text-align: center;
    font-weight: 700;
    font-size: 36px;
    line-height: 104%;
    text-transform: uppercase;
    color: $primary-color;
  }
  &__text {
    text-align: center;
    color: rgba(0, 0, 0, 0.8);
  }
  &__button {
    @include no-btn();
    margin: 0 auto;
    padding: 16px 74px;
    border-radius: 40px;
    color: #ffffff;
    background-color: $primary-color;
    transition: .2s;
    &:hover, &:active {
      background-color: $secondary-color;
    }
  }
}