.section-timer {
  padding: 30px 0;
  &__heading, &__text {
    text-align: center;
  }
  &__heading {
    margin-bottom: 10px;
    font-weight: 700;
    font-size: 72px;
    line-height: 104%;
    text-transform: uppercase;
    color: $primary-color;
    opacity: 0.39;
  }
  &__timer{
    margin: 35px 0 125px;
  }
  &__text {
    margin: 0 auto;
    max-width: 420px;
    font-size: 18px;
    line-height: 150%;
    color: rgba(0, 0, 0, 0.8)
  }
  &__link {
    @include no-btn();
    margin: 12px auto 0;
    padding: 16px 20px 16px 30px;
    max-width: 240px;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 30px;
    border-radius: 40px;
    background-color: #1D4169;
    text-decoration: none;
    transition: 0.2s;
    p {
      font-size: 18px;
      line-height: 150%;
      color: #ffffff;
    }
    &:hover {
      background-color: $secondary-color;
      outline: none;
    }
    &:visited {
      background-color: $primary-color;
    }
  }
}

@media (max-width: 1100px) {
  .section-timer {
    padding: 166px 0 176px;
    &__heading {
      margin-bottom: 20px;
      font-size: 36px;
    }
    &__text {
      max-width: 300px;
      font-size: 16px;
    }
    &__timer {
      margin: 40px 0 85px;
    }
  }
}
@media (max-width: 451px) {
  .section-timer {
    padding: 60px 0 90px;
    &__heading {
      margin-bottom: 20px;
      font-size: 36px;
    }
    &__text {
      max-width: 300px;
      font-size: 16px;
    }
    &__timer {
      margin: 40px 0 85px;
    }
  }
}