.timer {
  padding: 0;
  display: flex;
  justify-content: center;
  gap: 15px;
  &__item {
    list-style-type: none;
    p, span {
      user-select: none;
      font-weight: 600;
      font-size: 72px;
      line-height: 100%;
      color: #162C4E;
    }
    p {
      position: relative;
      width: 96px;
    }
  }
  &__amount-days::before, &__amount-hours::before, &__amount-minutes::before, &__amount-seconds::before {
    content: url("../img/sprite.svg#sprite--big-arc");
    position: absolute;
    bottom: -25px;
    left: -25px;
    width: 132px;
    height: 54px;
  }
  &__amount-days::after, &__amount-hours::after, &__amount-minutes::after, &__amount-seconds::after {
    position: absolute;
    bottom: -37px;
    right: 0;
    width: 100px;
    font-size: 16px;
    line-height: 100%;
    text-align: center;
    color: #ffffff;
  }
  &__amount-days::after {
    content: 'Days';
  }
  &__amount-hours::after {
    content: 'Hours';
  }
  &__amount-minutes::after {
    content: 'Minutes';
  }
  &__amount-seconds::after {
    content: 'Seconds';
  }
}

@media (max-width: 1100px) {
  .timer__item {
    p, span {
      font-size: 36px;
    }
    p {
      width: 49px;
    }
  }
  .timer__amount-days::before, .timer__amount-hours::before, .timer__amount-minutes::before, .timer__amount-seconds::before {
    content: url("../img/sprite.svg#sprite--arc");
    width: 64px;
    height: 40px;
    bottom: -35px;
    left: -10px;
  }
  .timer__amount-days::after, .timer__amount-hours::after, .timer__amount-minutes::after, .timer__amount-seconds::after {
    bottom: -30px;
    width: 55px;
    font-size: 12px;
  }
  .timer__amount-days::after {
    content: 'DD';
  }
  .timer__amount-hours::after {
    content: 'HH';
  }
  .timer__amount-minutes::after {
    content: 'MM';
  }
  .timer__amount-seconds::after {
    content: 'SS';
  }
}